@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
	height: 100%;
}

body {
	padding-top: 50px;
	background-color: #ffdf8c;
}

#root {
	width: 90%;
	margin: 0 auto;
	padding-top: 40px;
	padding-bottom: 30px;
	background: #ffffff;
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
}

.container {
	width: 95%;
	margin: 0 auto;
	padding: 0 15px;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}
